import React, { Component } from "react"
import { connect } from "react-redux"
import { changeAuth } from "../actions/changeAuth"
import { Formik, Form, Field, ErrorMessage } from "formik"
import * as Yup from "yup"
import Cookies from "js-cookie"

import auth from "../components/authValidation.module.scss"

const SignupSchema = Yup.object().shape({
  email: Yup.string()
    .email("Invalid email")
    .required("Required"),
  password: Yup.string()
    .min(8, "Too short.")
    .max(16, "Too long.")
    .matches(
      /^(?=.*[a-z])(?=.*[A-Z])(?=.*[0-9])(?=.*[!@#\$%\^&\*])/,
      "Must contain lowercase, uppercase, number, and special characters."
    )
    .required("Required"),
})

class UserAuth extends Component {
  state = {
    email: "",
    password: "",
    isError: false,
  }

  handleEmail = e => {
    this.setState({ email: e.target.value })
  }

  handlePassword = e => {
    this.setState({ password: e.target.value })
  }

  userSignIn = async (email, password) => {
    await fetch("https://stormy-basin-80765.herokuapp.com/getToken", {
      mode: "cors", // no-cors, cors, *include
      cache: "no-cache", // *default, no-cache, reload, force-cache, only-if-cached
      credentials: "include", // include, *include, omit
      headers: {
        "content-type": "application/json",
      },
      method: "GET",
    })
    const token = Cookies.get("XSRF-TOKEN")
    const data = await fetch(
      "https://stormy-basin-80765.herokuapp.com/signIn",
      {
        body: JSON.stringify({ email: email, password: password }),
        mode: "cors", // no-cors, cors, *include
        cache: "no-cache", // *default, no-cache, reload, force-cache, only-if-cached
        credentials: "include", // include, *include, omit
        headers: {
          "content-type": "application/json",
          "csrf-token": token,
        },
        method: "POST",
      }
    ).catch(error => this.setState({ isError: true }))
    if (data.ok) {
      console.log(data.ok)
      data.json().then(content => {
        console.log(content)
        this.props.changeAuth(content.authStatus)
        this.setState({
          email: "",
          password: "",
          isError: false,
        })
      })
    } else {
      this.setState({
        isError: true,
      })
    }
  }

  userSignUp = async (email, password) => {
    await fetch("https://stormy-basin-80765.herokuapp.com/getToken", {
      mode: "cors", // no-cors, cors, *include
      cache: "no-cache", // *default, no-cache, reload, force-cache, only-if-cached
      credentials: "include", // include, *include, omit
      headers: {
        "content-type": "application/json",
      },
      method: "GET",
    })
    const token = Cookies.get("XSRF-TOKEN")
    const data = await fetch(
      "https://stormy-basin-80765.herokuapp.com/signUp",
      {
        body: JSON.stringify({ email: email, password: password }),
        mode: "cors", // no-cors, cors, *include
        cache: "no-cache", // *default, no-cache, reload, force-cache, only-if-cached
        credentials: "include", // include, *include, omit
        headers: {
          "content-type": "application/json",
          "csrf-token": token,
        },
        method: "POST",
      }
    ).catch(error => this.setState({ isError: true }))
    if (data.ok) {
      console.log(data.ok)
      data.json().then(content => {
        console.log(content)
        this.props.changeAuth(content.authStatus)
        this.setState({
          email: "",
          password: "",
          isError: false,
        })
      })
    } else {
      this.setState({
        isError: true,
      })
    }
  }

  render() {
    return (
      <div className={auth.authBox}>
        <div className={auth.innerBox}>
          {this.state.isError && (
            <p className={auth.errorMessage}>Oops, something went wrong.</p>
          )}
          <input
            className=""
            placeholder="Email"
            type="text"
            value={this.state.email}
            onChange={this.handleEmail}
          />
          <input
            className=""
            placeholder="Password"
            type="password"
            value={this.state.password}
            onChange={this.handlePassword}
          />
          <div className={auth.buttons}>
            <button
              onClick={() => {
                this.userSignIn(this.state.email, this.state.password)
              }}
            >
              Sign In
            </button>
            <button
              onClick={() => {
                this.userSignUp(this.state.email, this.state.password)
              }}
            >
              Sing Up
            </button>
            <button
              onClick={() => {
                alert("Click Em")
              }}
            >
              Sing Out
            </button>
            <button
              onClick={() => {
                alert("auth")
              }}
            >
              Check Auth
            </button>
          </div>
          <Formik
            initialValues={{ email: "", password: "" }}
            validationSchema={SignupSchema}
            onSubmit={values => {
              alert(values.email)
              alert(JSON.stringify(values))
            }}
          >
            {({ errors, touched }) => (
              <Form>
                <Field placeholder="Email" name="email" />
                {errors.email && touched.email ? (
                  <p className={auth.errorMessage}>{errors.email}</p>
                ) : null}
                <Field type="password" placeholder="Password" name="password" />
                {errors.password && touched.password ? (
                  <p className={auth.errorMessage}>{errors.password}</p>
                ) : null}
                <button type="submit">Submit</button>
              </Form>
            )}
          </Formik>
        </div>
      </div>
    )
  }
}

function mapStateToProps(state) {
  return { isAuthed: state.authStatus }
}

export default connect(
  mapStateToProps,
  { changeAuth }
)(UserAuth)
